<template>
	<div class="page2_3_2">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="绩效审计" title="同行业比较疑点" id="more2_3_2" ydname="审计疑点线索挖掘"/>
			</div>
		</div>
		<div>
			<div class="lineBox box-border-shadow">
				<div v-if="showLs" id="thyLine" class="line" style="width: 100%;"></div>
				<img style="display:block;margin: 20px auto;" v-else src="../../../assets/no-sj.png">
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import * as echarts from 'echarts'
	import reqData from "../../../reqData.js"
	export default{
		name:'page2_3_2',
		data(){
			return{
				msg:["审计疑点线索挖掘","绩效审计","与同行业平均水平比较"],
				showLs:true
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			companyID(){
				return JSON.parse(this.$store.getters.getCompany).id;
			},
			getWord(){
				return this.$store.getters.getWord;
			},
			getWord1(){
				return this.$store.getters.getWord1;
			}
		},
		methods:{
			optionThyLine(data1,data2){
				let data11=data1[0];
				let data12=data1[1];
				if(data2){
					let data21=data2[0];
					let data22=data2[1];
					return {
						color:['#c23531','#c23531','#2f4554','#2f4554'],
		                title: {
		                    text: data11.text+" -- "+data21.text+"（参照指标） / 同行业对比",
		                    textStyle: {
		                        color:'#333',
		                        fontSize:22
		                    }
		                },
		                grid:{
		                    y:145,
		                    y2:20,
		                },
		                tooltip: {
		                    trigger: 'axis'
		                },
		                legend: {
		                    data:[{'name':data11.text},
		                        {'name':'同行业'+data11.text},
		                        {'name':data21.text},
		                        {'name':'同行业'+data21.text}],
		                    top:60
		                },
		                toolbox: {
		                    show: true,
		                    feature: {
		                        dataZoom: {
		                            yAxisIndex: 'none'
		                        },
		                        dataView: {readOnly: false},
		                        magicType: {type: ['line', 'bar']},
		                        restore: {},
		                        saveAsImage: {}
		                    }
		                },
		                xAxis:  {
		                    type: 'category',
		                    boundaryGap: false,
		                    data: data11.time
		                },
		                yAxis: [
		                    {
		                        name: data11.text + "(" + data11.danwei + ")",
		                        type: 'value',
		                        boundaryGap: [0.1, 0.1],
		                        scale: true,
		                        axisLabel: {
		                            formatter: '{value} ' + data11.danwei
		                        }
		                    },
		                    {
		                        name: data21.text + "(" + data21.danwei + ")",
		                        type: 'value',
		                        boundaryGap: [0.1, 0.1],
		                        scale: true,
		                        nameLocation: 'start',
		                        inverse: true,
		                        axisLabel: {
		                            formatter: '{value} ' + data21.danwei
		                        }
		                    }
		                ],
		                series: [
		                    {
		                        name:data11.text,
		                        type:'line',
		                        smooth: true,
		                        data:data11.value,
		                        lineStyle: {
		                            normal: {
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        }
		                    },
		                    {
		                        name:'同行业'+data11.text,
		                        type:'line',
		                        lineStyle: {
		                            normal: {
		                                type:'dashed',
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        },
		                        smooth: true,
		                        data:data12.value,

		                    },
		                    {
		                        name:data21.text,
		                        type:'line',
		                        yAxisIndex:1,
		                        smooth: true,
		                        data:data21.value,
		                        lineStyle: {
		                            normal: {
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        },
		                        markPoint: {
		                            data: data21.yiidan,
		                            itemStyle: {
		                                normal: {
		                                    color: 'rgb(47,69,84)'
		                                }
		                            },
		                        },
		                    },
		                    {
		                        name:'同行业'+data21.text,
		                        type:'line',
		                        yAxisIndex:1,
		                        lineStyle: {
		                            normal: {
		                                type:'dashed',
		                                width: 3,
		                                shadowColor: 'rgba(0,0,0,0.4)',
		                                shadowBlur: 10,
		                                shadowOffsetX: 4,
		                                shadowOffsetY: 10

		                            }
		                        },
		                        smooth: true,
		                        data:data22.value,

		                    }
		                ]
					}
				}
				return {
			            color:['#c23531','#c23531'],
			            title: {
			                text: data11.text+"/同行业对比",
			                textStyle: {
			                    color:'#333',
			                    fontSize:22
			                }
			            },
			            grid:{
			                y:145,
			                y2:20,
			            },
			            tooltip: {
			                trigger: 'axis'
			            },
			            legend: {
			                data:[data11.text,'同行业'+data11.text],
			                top:60
			            },
			            toolbox: {
			                show: true,
			                feature: {
			                    dataZoom: {
			                        yAxisIndex: 'none'
			                    },
			                    dataView: {readOnly: false},
			                    magicType: {type: ['line', 'bar']},
			                    restore: {},
			                    saveAsImage: {}
			                }
			            },
			            xAxis:  {
			                type: 'category',
			                boundaryGap: false,
			                data: data11.time
			            },
			            yAxis: {
			                    name: data11.text + "(" + data11.danwei + ")",
			                    type: 'value',
			                    boundaryGap: [0.1, 0.1],
			                    scale: true,
			                    axisLabel: {
			                        formatter: '{value} ' + data11.danwei
			                    }
			                },
			            series: [
			                {
			                    name:data11.text,
			                    type:'line',
			                    smooth: true,
			                    data:data11.value,
			                    lineStyle: {
			                        normal: {
			                            width: 3,
			                            shadowColor: 'rgba(0,0,0,0.4)',
			                            shadowBlur: 10,
			                            shadowOffsetX: 4,
			                            shadowOffsetY: 10

			                        }
			                    },
			                    markPoint: {
			                        data: data11.yiidan
			                    },
			                },
			                {
			                    name:'同行业'+data11.text,
			                    type:'line',
			                    lineStyle: {
			                        normal: {
			                            type:'dashed',
			                            width: 3,
			                            shadowColor: 'rgba(0,0,0,0.4)',
			                            shadowBlur: 10,
			                            shadowOffsetX: 4,
			                            shadowOffsetY: 10

			                        }
			                    },
			                    smooth: true,
			                    data:data12.value,

			                }
			            ]
			        };
			},
			getThyLine(){
				var myThyLine = echarts.init(document.getElementById('thyLine'));
				myThyLine.showLoading();
				let _this=this;
				reqData
			      .req({
			        apiName: "thyData",
			        data: {             
			          name:this.getWord1[0],
			          ID:this.companyID
			        }
			      })
			      .then(res => {
			        res = res.data;
			        if (res && res.code === "200") {
			          let data1 = res.data;
			          console.log(data1);
			          if(data1.length==0){
			          	  _this.showLs=false
			          }else{
				          if(_this.getWord1.length>1){
				          		reqData
							      .req({
							        apiName: "thyData",
							        data: {             
							          name:_this.getWord1[1],
							          ID:this.companyID
							        }
							      })
							      .then(res => {
							        res = res.data;
							        if (res && res.code === "200") {
							          let data2 = res.data;
							          console.log(data2);
							          myThyLine.hideLoading();
							          myThyLine.setOption(this.optionThyLine(data1,data2));  
							          sessionStorage.setItem("thyData", [JSON.stringify(data1),JSON.stringify(data2)]);
							        }
							      })
							      .catch(err => {
							        console.log(err);
							      });
				          }else{
				          	myThyLine.hideLoading();
				          	myThyLine.setOption(this.optionThyLine(data1));  
				          	sessionStorage.setItem("thyData", JSON.stringify(data1));
				          }
				      }
			        }
			      })
			      .catch(err => {
			        console.log(err);
			      });

			      $(window).resize(function(){
						myThyLine.resize({
							width:'auto',
							height:'auto'
						})
				  });
			}
		},
		mounted: function(){
			this.getThyLine();


		}
	}
</script>
<style type="text/css" scoped="">
	.lineBox{
		padding: 3% 5%;
		box-sizing: border-box;
	    width:100%;
	    height: 800px;
	    margin: 20px auto;
	}
	.lineBox .line{
		display: block; 
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}
</style>


